const AVATAR_LINK="https://wq.360buyimg.com/img/pingou-head/{{avatar}}.jpg";const options={template:"#avatar-loop",data:{avatar1:[],avatar2:[],avatarClass:"",idx:1,loop:1,len:5,timer:null,isList1:true,cls:"move1"},created(){this.initAvatar()},methods:{initAvatar(){this.setAvatarData(1);this.avatarLoop()},avatarLoop(){this.timer&&this.$clearTimeout(this.timer);this.timer=this.$setTimeout(()=>{this.move()},1500)},move(){this.avatarClass="move";let{idx:idx,len:len,loop:loop}=this;let loopLength=len*2;let list2FirstIdx=len+1;let cls="";if(idx==1){this.setAvatarData(2)}else if(idx==list2FirstIdx){this.loop=loop>=Math.floor(150/loopLength)?1:loop+1;this.setAvatarData(1)}idx+=1;this.idx=idx;if(idx==list2FirstIdx||idx==loopLength+1){cls=list2FirstIdx}else if(idx<list2FirstIdx){cls=idx%list2FirstIdx}else if(idx>list2FirstIdx){cls=(idx-len)%list2FirstIdx}this.cls=`move${cls}`;this.$setTimeout(()=>{this.avatarLoop();this.avatarClass="";this.$setTimeout(()=>{if(idx==list2FirstIdx||idx==loopLength+1){this.cls=`move1`}idx>loopLength&&(this.idx=1);this.isList1=this.idx<=this.len},100)},500)},setAvatarData(type){const avatar1=[];const avatar2=[];const{len:len,loop:loop}=this;let loopLength=len*2;let begin1=loopLength*(loop-1);let end1=loopLength*loop-len;if(type==1){for(let i=begin1;i<end1;i++){avatar1.push(AVATAR_LINK.replace("{{avatar}}",i))}this.avatar1=avatar1}if(type==2){for(let j=end1;j<end1+len;j++){avatar2.push(AVATAR_LINK.replace("{{avatar}}",j))}this.avatar2=avatar2}}}};export default options;