import{request}from"../../../../common/request/request";import{mergeObjRecursive,isWqXcxEnv,JD,isXCX,isMEnv}from"../common/util";import*as login from"../../../../common/login/loginv1";const ERR_CODE_MAP={10001:"未登录",10002:"频率限制",10003:"非正式pin",10004:"系统繁忙",10005:"参数错误",10006:"未设置频率规则",10007:"redis访问失败",10008:"用户黑名单",10009:"查询配置失败",10010:"活动未开始",10011:"活动已结束"};const LOGIN_FAIL_COUNT_LIMIT=3;const UMP_REPORT_ERR_CODE_MAP=["10004","10005","10007","10009"];const defaultUnLogin=function(res){return[10001,2e4].includes(res.ret)};function customRequest(_option={},doLogin=true,_page={}){const defaultOption={method:"GET",dataType:isXCX?"":"jsonp",data:{},ump:{reportHook:reportHook}};let loginCount=0;const option=mergeObjRecursive(defaultOption,_option);if(option.ump&&option.ump.bizId&&!option.ump.errBizId){option.ump.errBizId=option.ump.bizId;option.ump.errOpId=option.ump.opId+1e3}function reqFunc(){return new Promise((resolve,reject)=>{request.get(option).then(({body:body})=>{option.needLogin=option.needLogin||defaultUnLogin;if(option.needLogin(body)){if(!doLogin&&!isWqXcxEnv){resolve({body:body});return}if(loginCount>=LOGIN_FAIL_COUNT_LIMIT&&!isMEnv){reject({code:999,message:"可能是网络出现了问题哦，请稍后再试试吧~"});return}login.doLogin().then(()=>{loginCount++;if(!isXCX)return;resolve(reqFunc())})}else{resolve({body:body})}}).catch(e=>{console.error(e);reject({ret:e.code||999,msg:ERR_CODE_MAP[999]})})})}return reqFunc()}function formatMsg(res={}){const getCookie=JD.cookie.get;const msg=`pin: ${decode(getCookie("pin")||getCookie("pt_pin"))}, uin: ${getCookie("wq_uin")}, nickname: ${decode(getCookie("nickName")||getCookie("nickname"))}, wxNickname: ${decode(getCookie("wxNickName")||getCookie("wx_nickname"))}, `;const code=`code: ${res.ret}`;const source=isXCX?{source:""}:`source: ${location.href}`;if(res.ret==0){return"success"}const result=`${code} \n ${msg} \n ${source} \n ${JSON.stringify(res).slice(0,200)}`;return result;function decode(s){try{return decodeURIComponent(s)}catch(e){return s}}}function reportHook(data){const isErr=UMP_REPORT_ERR_CODE_MAP.indexOf(data.ret+"")!==-1;const code=isErr?1:0;return{code:code,msg:formatMsg(data)}}export{request,customRequest,ERR_CODE_MAP};