import*as Modal from"../../../../common/modal/modal";import{ENV,getCurrentPageUrl,addUrlParam}from"./util";const H5_BIND_URL="https://wqs.jd.com/my/bindpopupv2/index.shtml";const XCX_BIND_URL="/pages/my_pages/accountv2/index";export function showBindModal(_options){const defaultOpt={title:"温馨提示",content:"",sceneid:"721194748",ptag:"138268.6.13",returnBtnText:"返回首页",returnUrl:getCurrentPageUrl(),goH5Bind:false,confirmText:"去绑定"};const option=Object.assign({},defaultOpt,_options);const pageParams={sceneid:encodeURIComponent(option.sceneid),ptag:encodeURIComponent(option.ptag),returnText:encodeURIComponent(option.returnBtnText),returnUrl:encodeURIComponent(option.returnUrl)};const page=_options.page||{};const succCb=getBindSuccCb(option.goH5Bind,pageParams,page);Modal.show({title:option.title,content:option.content,confirmText:option.confirmText,showCancel:false,success:succCb})}function getBindSuccCb(goH5Bind,_params,page){let succCb=null;if(ENV.isXcxEnv){succCb=callbackByXcx(goH5Bind,_params,page)}else{succCb=callbackByH5(_params)}return succCb}function callbackByH5(_params){return()=>{const bindUrl=addUrlParam(H5_BIND_URL,_params);setTimeout(()=>{window.location.href=bindUrl},50)}}function callbackByXcx(goH5Bind,_params,page){if(!page||!page.$goto){return}if(goH5Bind){return()=>{const bindUrl=addUrlParam(H5_BIND_URL,_params);page.$goto("/pages/h5/index",{url:bindUrl},"redirectTo")}}if(ENV.isMainWxApp||ENV.isPgWXApp){return()=>{page.$goto(XCX_BIND_URL,_params,"redirectTo")}}return()=>{}}